.landing-container {
  font-family: sans-serif;
  color: white;
  background: linear-gradient(to bottom, #1a237e, #4a148c);
  min-height: 100vh;
}

.logo-section {
  position: relative;
  height: 100vh;
  overflow: hidden;
  background: black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo-full {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

.logo-curve {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, #1a237e 100%);
  border-bottom-left-radius: 50% 20%;
  border-bottom-right-radius: 50% 20%;
}

.landing-main {
  padding: 2rem;
  text-align: center;
}

.landing-footer {
  background: rgba(0,0,0,0.5);
  padding: 2rem;
  text-align: center;
}

.signup-form {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.signup-form input {
  padding: 0.5rem;
  border-radius: 8px;
  border: none;
  width: 250px;
}

.signup-form button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 8px;
  background: #7b1fa2;
  color: white;
  cursor: pointer;
}

.signup-form button:hover {
  background: #9c27b0;
}

.feature-list {
  list-style: disc;
  padding-left: 1.5rem;
  max-width: 600px;
  margin: 1rem auto;
  text-align: left;
}

.sweet-dreams {
  font-style: italic;
  margin-top: 1rem;
}

.gif-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-top: 2rem;
}

.gif {
  width: 100%;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.5);
  object-fit: cover;
}
